import React from "react"

export default ({ data, fixed, active }) => {

  return (
    <nav className={`navbar ${fixed ? 'fixed' : ''}`}>
      <div className="container">
        <i className="icon icon-menu"></i>
        <a className="logo" href="/"></a>
        <i className="icon icon-close"></i>
        <ul className="navbar-nav">
          {data.site.siteMetadata.navbar.navs.map((nav, i) => {
            const navbar = data.site.siteMetadata.navbar;

            return (
              <li className="nav-item" key={i}> 
                <a className={`link ${active === navbar[nav] ? 'active' : ''}`} href={`${navbar['href' + (i + 1)]}`}>
                  {navbar[nav]}
                </a>
              </li>
            );
          })}
        </ul>
      </div>
    </nav>
  )
}


